namespace $ {
	export class $hyoo_shelter_auth_form extends $mol_page {
		
		/**
		 * ```tree
		 * title \Вход в систему управления приютов
		 * ```
		 */
		title() {
			return "Вход в систему управления приютов"
		}
		
		/**
		 * ```tree
		 * plugins / <= Enter
		 * ```
		 */
		plugins() {
			return [
				this.Enter()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * body / <= Form
		 * ```
		 */
		body() {
			return [
				this.Form()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * submit?event null
		 * ```
		 */
		@ $mol_mem
		submit(event?: any) {
			if ( event !== undefined ) return event as never
			return null as any
		}
		
		/**
		 * ```tree
		 * Enter $mol_hotkey key * enter?event <=> submit?event
		 * ```
		 */
		@ $mol_mem
		Enter() {
			const obj = new this.$.$mol_hotkey()
			
			obj.key = () => ({
				enter: (event?: any) => this.submit(event)
			})
			
			return obj
		}
		
		/**
		 * ```tree
		 * Login_name \Логин
		 * ```
		 */
		Login_name() {
			return "Логин"
		}
		
		/**
		 * ```tree
		 * login?val \user
		 * ```
		 */
		@ $mol_mem
		login(val?: any) {
			if ( val !== undefined ) return val as never
			return "user"
		}
		
		/**
		 * ```tree
		 * Login $mol_select
		 * 	value?val <=> login?val
		 * 	dictionary *
		 * 		user \ЖКХ
		 * 		gbu1 \ГБУ_1
		 * 		gbu2 \ГБУ_2
		 * 		gbu3 \ГБУ_3
		 * 		gbu4 \ГБУ_4
		 * 		gbu5 \ГБУ_5
		 * 		gbu6 \ГБУ_6
		 * 		gbu7 \ГБУ_7
		 * 		gbu8 \ГБУ_8
		 * 		gbu9 \ГБУ_9
		 * 		gbu10 \ГБУ_10
		 * 		gbu11 \ГБУ_11
		 * 		gbu12 \ГБУ_12
		 * 		gbu13 \ГБУ_13
		 * 		admin1 \Префектура_1
		 * 		admin2 \Префектура_2
		 * 		admin3 \Префектура_3
		 * 		admin4 \Префектура_4
		 * 		admin5 \Префектура_5
		 * 		admin6 \Префектура_6
		 * 		admin7 \Префектура_7
		 * 		admin8 \Префектура_8
		 * 		trustee1 \Приют_1
		 * 		trustee2 \Приют_2
		 * 		trustee3 \Приют_3
		 * 		trustee4 \Приют_4
		 * 		trustee5 \Приют_5
		 * 		trustee6 \Приют_6
		 * 		trustee7 \Приют_7
		 * 		trustee8 \Приют_8
		 * 		trustee9 \Приют_9
		 * 		trustee10 \Приют_10
		 * 		trustee11 \Приют_11
		 * 		trustee12 \Приют_12
		 * 		trustee13 \Приют_13
		 * ```
		 */
		@ $mol_mem
		Login() {
			const obj = new this.$.$mol_select()
			
			obj.value = (val?: any) => this.login(val)
			obj.dictionary = () => ({
				user: "ЖКХ",
				gbu1: "ГБУ_1",
				gbu2: "ГБУ_2",
				gbu3: "ГБУ_3",
				gbu4: "ГБУ_4",
				gbu5: "ГБУ_5",
				gbu6: "ГБУ_6",
				gbu7: "ГБУ_7",
				gbu8: "ГБУ_8",
				gbu9: "ГБУ_9",
				gbu10: "ГБУ_10",
				gbu11: "ГБУ_11",
				gbu12: "ГБУ_12",
				gbu13: "ГБУ_13",
				admin1: "Префектура_1",
				admin2: "Префектура_2",
				admin3: "Префектура_3",
				admin4: "Префектура_4",
				admin5: "Префектура_5",
				admin6: "Префектура_6",
				admin7: "Префектура_7",
				admin8: "Префектура_8",
				trustee1: "Приют_1",
				trustee2: "Приют_2",
				trustee3: "Приют_3",
				trustee4: "Приют_4",
				trustee5: "Приют_5",
				trustee6: "Приют_6",
				trustee7: "Приют_7",
				trustee8: "Приют_8",
				trustee9: "Приют_9",
				trustee10: "Приют_10",
				trustee11: "Приют_11",
				trustee12: "Приют_12",
				trustee13: "Приют_13"
			})
			
			return obj
		}
		
		/**
		 * ```tree
		 * Login_field $mol_form_field
		 * 	name <= Login_name
		 * 	control <= Login
		 * ```
		 */
		@ $mol_mem
		Login_field() {
			const obj = new this.$.$mol_form_field()
			
			obj.name = () => this.Login_name()
			obj.control = () => this.Login()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Pass_name \Пароль
		 * ```
		 */
		Pass_name() {
			return "Пароль"
		}
		
		/**
		 * ```tree
		 * pass?val \password
		 * ```
		 */
		@ $mol_mem
		pass(val?: any) {
			if ( val !== undefined ) return val as never
			return "password"
		}
		
		/**
		 * ```tree
		 * Pass $mol_string
		 * 	type \password
		 * 	value?val <=> pass?val
		 * 	enabled false
		 * ```
		 */
		@ $mol_mem
		Pass() {
			const obj = new this.$.$mol_string()
			
			obj.type = () => "password"
			obj.value = (val?: any) => this.pass(val)
			obj.enabled = () => false
			
			return obj
		}
		
		/**
		 * ```tree
		 * Pass_field $mol_form_field
		 * 	name <= Pass_name
		 * 	control <= Pass
		 * ```
		 */
		@ $mol_mem
		Pass_field() {
			const obj = new this.$.$mol_form_field()
			
			obj.name = () => this.Pass_name()
			obj.control = () => this.Pass()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Auth_label \Войти
		 * ```
		 */
		Auth_label() {
			return "Войти"
		}
		
		/**
		 * ```tree
		 * auth?val null
		 * ```
		 */
		@ $mol_mem
		auth(val?: any) {
			if ( val !== undefined ) return val as never
			return null as any
		}
		
		/**
		 * ```tree
		 * Submit $mol_button_major
		 * 	title <= Auth_label
		 * 	click?val <=> auth?val
		 * ```
		 */
		@ $mol_mem
		Submit() {
			const obj = new this.$.$mol_button_major()
			
			obj.title = () => this.Auth_label()
			obj.click = (val?: any) => this.auth(val)
			
			return obj
		}
		
		/**
		 * ```tree
		 * Form $mol_form
		 * 	form_fields /
		 * 		<= Login_field
		 * 		<= Pass_field
		 * 	buttons / <= Submit
		 * ```
		 */
		@ $mol_mem
		Form() {
			const obj = new this.$.$mol_form()
			
			obj.form_fields = () => [
				this.Login_field(),
				this.Pass_field()
			] as readonly any[]
			obj.buttons = () => [
				this.Submit()
			] as readonly any[]
			
			return obj
		}
	}
	
}

